@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: #eaf2ff;
  font-family: 'Poppins',
    sans-serif;
  flex-direction: column;
}

.App .DeckWrapper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.Navbar {
    background-color: #3b55e6;
    height: 10%;
    width: 100%;
    color: white;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Navbar .title {
    font-size: 1.6rem;
    margin-left: 1.5%;
    font-weight: 200;
}

.Navbar .profilePic {
    height: 70%;
    border-radius: 50%;
    margin-right: 1.5%;
}
.Sidebar {
    width: 18%;
    height: 90%;
    background-color: white;
    padding-top: 1%;
    display: flex;
    justify-content: center;
}

.Sidebar .menuList {
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-left: 1%;
    width: 90%;
}

.Sidebar .menuList .menuItem {
    text-align: left;
    margin-top: 3%;
    color: #a2a7b0;
    font-size: 1.1rem;
    margin-right: 2%;
    display: flex;
    width: 100%;
}

.Sidebar .menuList .menuItem a {
    padding: 4%;
    border-radius: 15px;
    width: 100%;
    color: #a2a7b0;
    text-decoration: none;
}

.Sidebar .menuList .menuItem .active {
    background-color: #eaf2ff;
    color: #508ff4;
}

.Sidebar .menuList .menuItem i {
    margin-right: 3%;
    margin-left: 3%;
}
.Deck {
    width: 82%;
    height: 87%;
    padding: 3%;
    overflow-y: hidden;
}

.Deck .headerRow {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 2%;
}
.InvoicesProcessedCard {
    background-color: white;
    border-radius: 5%;
    width: 20%;
    display: flex;
    flex-direction: column;
}

.InvoicesProcessedCard .number {
    font-size: 4rem;
    font-weight: 100;
}

.InvoicesProcessedCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}
.UploadCard {
    background-color: white;
    border-radius: 5%;
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 2px solid #729fe5;
    cursor: pointer;
}

.UploadCard:hover {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.UploadCard .image {
    font-size: 4rem;
    font-weight: 100;
    margin-top: 7%;
    color: #a2a7b0;
}

.UploadCard:hover .image {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.UploadCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}
.DownloadCard {
    background-color: white;
    border-radius: 5%;
    width: 30%;
    display: flex;
    flex-direction: column;
    border: 2px solid #729fe5;
    cursor: pointer;
    align-items: center;
    justify-content: space-around;
}

.DownloadCard:hover {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.DownloadCard .image {
    font-size: 4rem;
    font-weight: 100;
    margin-top: 7%;
    color: #a2a7b0;
}

.DownloadCard:hover .image {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.DownloadCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}

.DownloadCard .circleProgress {
    width: 70px;
    margin-top: 3%;
}
.overlay {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.5;
    top: 0;
    left: 0;
}

.uploader {
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80%;
    width: 40%;
    background-color: white;
    border-radius: 2%;
    box-shadow: 0 3px 3px 0 rgba(196, 200, 208, 0.7);
    overflow: hidden;
}

.uploader .header {
    height: 5%;
    width: 100%;
    background-color: #508ff4;
    text-align: right;
}

/* width */
.uploader ::-webkit-scrollbar {
    width: 2px;
}

/* Track */
.uploader ::-webkit-scrollbar-track {
    border-radius: 10px;
}

/* Handle */
.uploader ::-webkit-scrollbar-thumb {
    background: #508ff4;
    border-radius: 10%;
}

/* Handle on hover */
.uploader ::-webkit-scrollbar-thumb:hover {
    background: #508ff4;
}

.loader {
    border: 0.3rem solid #f3f3f3;
    border-radius: 50%;
    border-top: 0.3rem solid #3498db;
    width: 0.7rem;
    height: 0.7rem;
    -webkit-animation: spin 0.5s linear infinite;
    /* Safari */
    animation: spin 0.5s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
.ProcessedViewer {
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 90%;
    width: 50%;
    background-color: white;
    border-radius: 2%;
    box-shadow: 0 3px 3px 0 rgba(196, 200, 208, 0.7);
    overflow: hidden;
    display: flex;
}

.ProcessedViewer .fields {
    width: 35%;
    height: 100%;
    background-color: #eaf2ff;
    padding-top: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ProcessedViewer .preview {
    width: 100%;
    height: 100%;
    background-repeat: round;
}

.ProcessedViewer .fields .title {
    color: #3b55e6;
    font-weight: 600;
    font-size: 2rem;
    display: flex;
}

.ProcessedViewer .fields label {
    margin-top: 5%;
    width: 80%;
    text-align: left;
}

.ProcessedViewer .fields input {
    background-color: white;
    border: 0;
    border-radius: 16px;
    width: 80%;
    padding: 2%;
    padding-left: 4%;
    caret-color: #3b55e6;
}

.ProcessedViewer .fields input:focus {
    outline: none;
}

.ProcessedViewer .crossButton {
    position: absolute;
    right: 10px;
    top: 50px;
    font-size: 1rem;
    cursor: pointer;
}

.ProcessedViewer .crossButton .icon-background {
    color: #3b55e6;
}

.formWrapper {
  background-color: white;
  width: 90%;
  height: 90%;
  border-radius: 1%;
  display: flex;
  justify-items: flex-start;
  flex-direction: row;
  margin: auto;
  overflow: hidden;
}

.formWrapper .title {
  font-size: 1.5rem;
  font-weight: 500;
}

.formWrapper .fields {
  width: 30%;
  display: flex;
  flex-direction: column;
  background-color: #3b55e6;
}

.formWrapper .fields a {
  text-decoration: none;
}

.formWrapper .fields .heading {
  color: white;
  margin-top: 10%;
  font-size: 1.5rem;
}

.formWrapper .fields .pages {
  display: flex;
  flex-direction: column;
  /* justify-content: center;
  align-items: center; */
  height: 100%;
  margin-top: 20%;
}

.formWrapper .fields .pages .pageLabel {
  color: white;
  margin-bottom: 5%;
  padding: 5%;
  font-size: 1.2rem;
  transform: translateX(10px);
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

.formWrapper .fields .pages .active {
  background-color: white;
  color: #3b55e6;
}

.formWrapper .formDetails {
  width: 70%;
  padding: 3%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: scroll;
}

.formWrapper .formDetails .inputFields {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.formWrapper .formDetails .customInput {
  width: 70%;
  margin-top: 5%;
}

.formWrapper .formDetails .formControl {
  margin-top: 5%;
}

.formWrapper .formDetails .customSelect {
  width: 70%;
  margin-top: 5%;
  text-align: left;
}

.formWrapper .formDetails .info {
  width: 70%;
  margin-top: 5%;
  text-align: left;
  color: #3b55e6;
}

.formWrapper .formDetails .coInvestigatorsRow {
  width: 95%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.formWrapper .formDetails .coInvestigatorsRow .inputField {
  margin-top: 5%;
  margin-right: 5%;
  width: 40%;
}

.formWrapper .formDetails .coInvestigatorsRow .icons {
  margin-top: 5%;
  margin-left: 3%;
  color: #3b55e6;
  cursor: pointer;
}

.formWrapper .formDetails .nextButton {
  background-color: #3b55e6;
  color: white;
  padding: 2%;
  width: 10%;
  border-radius: 10px;
  text-decoration: none;
  cursor: pointer;
}

.formWrapper .formDetails .nextButtonDisabled {
  background-color: grey;
  color: white;
  padding: 2%;
  width: 10%;
  border-radius: 10px;
  text-decoration: none;
}

.formWrapper .formDetails .lastRow {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-self: flex-end;
}

/* width */
.formWrapper ::-webkit-scrollbar {
  width: 2px;
}

/* Track */
.formWrapper ::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
.formWrapper ::-webkit-scrollbar-thumb {
  background: #508ff4;
  border-radius: 10%;
}

/* Handle on hover */
.formWrapper ::-webkit-scrollbar-thumb:hover {
  background: #508ff4;
}

.InvoiceViewer {
    position: fixed;
    width: 60%;
    height: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
}

.InvoiceViewer .crossButton {
    position: absolute;
    z-index: 10;
    font-size: 1rem;
    right: 23px;
    top: 47px;
    cursor: pointer;
}
